.custom-div-head{
	background-color: transparent;
	color: white;
}
.navbar-collapse{
	justify-content: center;
}
.dropdown:hover>.dropdown-menu {
  display: block;
}
.nav-item{
	font-family: 'Bungee', cursive;
	font-size: 20px;
}
.nav-link:focus{
	color:#FF4136;
}
.navbar-light .navbar-nav .nav-link{
	color: white;
}
.sticky-top{
	background-color: black;
}
.sticky-top .navbar-nav .nav-link{
	color: white;
}
.sticky-top .navbar-nav .nav-link:focus, .sticky-top .navbar-nav .nav-link:hover{
	color: #FF725C;
}