.back-cus{
	background-image: url('../img/free.jpg');
	height: 400px;
}
.cus-free{
	color: white;
	text-align: center;
	margin: 0px;
}
.cus-left{
	color: white;
	text-align: left;
	font-size: 40px;
	cursor: pointer;
}
.cus-right{
	color: white;
	text-align: right;
	font-size: 40px;
	cursor: pointer;
}
.Tilt{
	cursor: pointer;
}
@media screen and (max-width: 600px){
	.cus-left{
		text-align: center;
		font-size: 25px;
	}
	.cus-right{
		text-align: center;
		font-size: 25px;
	}
}