* {
  box-sizing: border-box;
}
.cus-a{
  text-decoration: none;
  color: #FF725C;
  font-size:18px;
}
.cus-time-h{
  font-size: 40px;
  text-align: center;
}
 .cus-p{
    text-align: left;
  }
.cus-time-prev{
  color: white;
  text-align: center;
}
.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}
.timeline::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: #111111;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}
/* Container around content */
.cus-con {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

/* The circles on the timeline */
.cus-con::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  background-color: #111111;
  border: 4px solid #FF9F55;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}
.left {
  right: 27%;
}

/* Place the container to the right */
.right {
  left: 27%;
}
.left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid #111111;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent #111111;
}
.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid #111111;
  border-width: 10px 10px 10px 0;
  border-color: transparent #111111 transparent transparent;
}.right::after {
  left: -16px;
}
.content {
  padding: 20px 30px;
  background-color: #111111;
  position: relative;
  border-radius: 6px;
  color: white;
}
/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 850px) {
/* Place the timelime to the left */
  .timeline::after {
    left: 31px;
  }

/* Full-width containers */
  .container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

/* Make sure that all arrows are pointing leftwards */
  .container::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }

/* Make sure all circles are at the same spot */
  .left::after, .right::after {
    left: 15px;
  }

/* Make all right containers behave like the left ones */
  .right {
    left: 0%;
  }
   .left {
    left: 0;
  }
  .cus-time-h{
    font-size: 30px
  }
}
